import "../css/nav.css";
import React, { useState } from "react";
const Nav = ({ onSelectMenu }) => {
  const [subMenu1, setSubMenu1] = useState(true);
  const [selectedMenu, setSelectedMenu] = useState(" ");

  const handleSubMenuClick = (menu) => {
    onSelectMenu(menu);
    setSelectedMenu(menu);
  };

  const renderSubMenu = (menu) => (
    <div
      className={`sub_menu ${selectedMenu === menu ? "selected" : ""}`}
      style={selectedMenu === menu ? { border: "2px solid #cabd99" } : {}}
      onClick={() => handleSubMenuClick(menu)}
    >
      {menu}
    </div>
  );
  return (
    <div className="nav">
      <div
        className="menu"
        onClick={() => {
          setSubMenu1(!subMenu1);
          if (!subMenu1) {
            handleSubMenuClick(" ");
          }
        }}
      >
         
      </div>
      {subMenu1 ? (
        <>
          {renderSubMenu(" ")}
          {renderSubMenu("")}
          {renderSubMenu("")}
        </>
      ) : (
        ""
      )}
      <div
        className="menu"
        onClick={() => {
          handleSubMenuClick(" ");
        }}
      >
         
      </div>
      <div
        className="menu"
        onClick={() => {
          handleSubMenuClick(" ");
        }}
      >
         
      </div>
      <div
        className="menu"
        onClick={() => {
          handleSubMenuClick(" ");
        }}
      >
         
      </div>
    </div>
  );
};

export default Nav;
