import "../css/detail.css";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { LOAD_CAREER_REQUEST } from "../reducers/adminIndividual";
const CareerDetail = ({ onSelectMenu, data }) => {
  const individualId = data.individualId;
  const { career } = useSelector((state) => state.adminIndividual);
  console.log("career : ", career);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch({
      type: LOAD_CAREER_REQUEST,
      data: {
        individualId,
      },
    });
  }, []);
  return (
    <div className="detail individual">
      <div className="detail_container">
        <div
          className="back_btn"
          onClick={() => {
            onSelectMenu("");
          }}
        >
          
        </div>
        <div className="title"></div>
        {career && (
          <>
            <div className="text_box">
              <p className="text_title" style={{ fontWeight: "700" }}>
                1.
              </p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{career.user_career_company1}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{career.user_career_position1}</p>
            </div>
            <div className="text_box">
              <p className="text_title">   </p>
              <p className="text_content">{career.user_career_companyState1}</p>
            </div>
            <div className="text_box">
              <p className="text_title"> </p>
              <p className="text_content">{career.user_career_program1}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{`${career.user_career_period11Year} ${career.user_career_period11Month} ~ ${career.user_career_period12Year} ${career.user_career_period12Month}`}</p>
            </div>
            <div className="text_box">
              <p className="text_title" style={{ fontWeight: "700" }}>
                1.
              </p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{career.user_career_license1}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{`${career.user_career_license1Year} ${career.user_career_license1Month} ${career.user_career_license1Day}`}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">
                {career.user_career_license1Organization}
              </p>
            </div>
            <div className="text_box">
              <p className="text_title" style={{ fontWeight: "700" }}>
                 
              </p>
            </div>
            <div className="text_box">
              <p className="text_title"> </p>
              <p className="text_content">{career.user_career_trainingName1}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{`${career.user_career_trainingPeriod11Year} ${career.user_career_trainingPeriod11Month} ~ ${career.user_career_trainingPeriod12Year} ${career.user_career_trainingPeriod12Month}`}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">
                {career.user_career_trainingDetail1}
              </p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">
                {career.user_career_trainingOrganization}
              </p>
            </div>
            <div className="text_box">
              <p className="text_title" style={{ fontWeight: "700" }}>
                
              </p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{`${career.user_career_changeYear} ${career.user_career_changeMonth}`}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{career.user_career_changeReason}</p>
            </div>
            <div className="text_box">
              <p className="text_title" style={{ fontWeight: "700" }}>
                   
              </p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{career.user_career_knowCount}</p>
            </div>
            <div className="text_box">
              <p className="text_title">()</p>
              <p className="text_content">{career.user_career_knowTax}</p>
            </div>
            <div className="text_box">
              <p className="text_title" style={{ fontWeight: "700" }}>
                  
              </p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{`
              ${career.user_career_abilityProcess1 ? "" : ""} 
              ${
                career.user_career_abilityProcess2 ? "" : ""
              }          
              ${
                career.user_career_abilityProcess3 ? "" : ""
              }          
              ${
                career.user_career_abilityProcess4 ? "" : ""
              }          
              ${career.user_career_abilityProcess5 ? "" : ""}
              `}</p>
            </div>
            <div className="text_box">
              <p className="text_title"></p>
              <p className="text_content">{`
              ${career.user_career_abilityDrive1 ? "" : ""} 
              ${career.user_career_abilityDrive2 ? "" : ""} 
              `}</p>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default CareerDetail;
