import "../css/detail.css";
import "../css/table.css";
import "../css/processDetail.css";
import React, { useEffect, useState, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  LOAD_MATCHINGLISTS_REQUEST,
  DELETE_MATCHINGLIST_REQUEST,
  ADD_MATCHINGLIST_REQUEST,
} from "../reducers/adminBusiness";
import { LOAD_ALLRESUMES_REQUEST } from "../reducers/adminIndividual";
const ProcessDetail = ({ onSelectMenu, data }) => {
  const dispatch = useDispatch();
  const { matchingLists, deleteMatchingListDone, loadMatchingListsDone } =
    useSelector((state) => state.adminBusiness);
  const { userIndividuals } = useSelector((state) => state.adminUser);
  const { allResumes } = useSelector((state) => state.adminIndividual);
  const businessId = data.businessId;
  const [modalOpen, setModalOpen] = useState(false);
  const [modalData, setModalData] = useState();
  const handleModal = (data) => {
    setModalData(data);
    setModalOpen(true);
  };
  const uniqueMatchingLists = Array.from(
    new Set(matchingLists.map((list) => list.id))
  ).map((id) => matchingLists.find((list) => list.id === id));
  // console.log("matchingLists : ", matchingLists);
  useEffect(() => {
    dispatch({
      type: LOAD_MATCHINGLISTS_REQUEST,
      data: {
        businessId,
      },
    });
  }, [dispatch, deleteMatchingListDone]);
  useEffect(() => {
    dispatch({
      type: LOAD_ALLRESUMES_REQUEST,
    });
  }, []);

  let rank1, rank2, rank3, rank4;
  let rankList1, rankList2, rankList3, rankList4;

  const assignRank = (value, text) => {
    if (value === "1") {
      rank1 = text;
    } else if (value === "2") {
      rank2 = text;
    } else if (value === "3") {
      rank3 = text;
    } else if (value === "4") {
      rank4 = text;
    }
  };

  assignRank(data.list.business_application_rank1, "  ");

  if (rank1 === "  ") {
    assignRank(data.list.business_application_rank2, "");
    assignRank(data.list.business_application_rank3, "");
    assignRank(data.list.business_application_rank4, "");
  } else {
    assignRank(data.list.business_application_rank2, "");
    assignRank(data.list.business_application_rank3, "");
    assignRank(data.list.business_application_rank4, "");
  }

  function assignRankList(rank, data) {
    if (rank === "") {
      let career = "";
      if (data.list.business_application_career === "") {
        career = "";
      } else if (data.list.business_application_career === "") {
        career = "";
      } else {
        career = `${data.list.business_application_career1}`;
      }
      return career;
    }
    return null;
  }

  rankList1 = assignRankList(rank1, data);

  if (rank1 === "") {
    rankList2 = assignRankList(rank2, data);

    if (rank2 === "  ") {
      rankList2 = `${data.list.business_application_startYear} ${data.list.business_application_startMonth}`;

      if (rank3 === "") {
        let workType = "";
        if (data.list.business_application_workPlace === " ") {
          workType = "";
        } else if (
          data.list.business_application_workPlace === "  "
        ) {
          workType = "";
        } else if (
          data.list.business_application_workPlace ===
          "   ()"
        ) {
          workType = "";
        }
        rankList3 = workType;

        if (rank4 === "") {
          rankList4 = data.business.business_member_jibunAddress;
        }
      }
    }
  } else if (rank2 === "") {
    rankList2 = assignRankList(rank2, data);
  }

  const deleteList = useCallback(
    (e, id, cnt) => {
      const isConfirmed = window.confirm("?");
      const newCnt = cnt - 1;
      setUserCnt(newCnt);
      if (isConfirmed) {
        e.preventDefault();
        dispatch({
          type: DELETE_MATCHINGLIST_REQUEST,
          data: {
            id,
            newCnt,
          },
        });
      }
    },
    [dispatch]
  );

  const addList = useCallback(
    (e, id, cnt) => {
      const isConfirmed = window.confirm("?");
      const newCnt = Number(cnt) + 1;
      setUserCnt(String(newCnt));
      if (isConfirmed) {
        e.preventDefault();
        dispatch({
          type: ADD_MATCHINGLIST_REQUEST,
          data: {
            id,
            newCnt,
          },
        });
      }
    },
    [dispatch]
  );

  const [userCnt, setUserCnt] = useState(data.list.individualCnt);

  return (
    <div className="detail processDetail">
      <div className="detail_container">
        <div
          className="back_btn"
          onClick={() => {
            onSelectMenu(data.previousComponent);
          }}
        >
          
        </div>
        <div className="user_box">
          <p> : {data.business.business_member_companyName}</p>
          <p> : {data.list.business_application_name}</p>
        </div>
        <div className="title">  : {data.list.progressStep}</div>
        <p className="rank"> </p>
        <div className="rank_box">
          <p>1</p>
          <p>{rank1}</p>
          <p>{rankList1}</p>
        </div>
        <div className="rank_box">
          <p>2</p>
          <p>{rank2}</p>
          <p>{rankList2}</p>
        </div>
        <div className="rank_box">
          <p>3</p>
          <p>{rank3}</p>
          <p>{rankList3}</p>
        </div>
        <div className="rank_box">
          <p>4</p>
          <p>{rank4}</p>
          <p>{rankList4}</p>
        </div>
        <p className="rank">  </p>
        <div className="table_container">
          <div className="row row_head">
            <p>No</p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
          {uniqueMatchingLists &&
            uniqueMatchingLists
              .filter((list) => Number(list.BusinessId) === businessId)
              .map((list, index) => {
                const user =
                  userIndividuals &&
                  userIndividuals.find(
                    (user) => user.IndividualId === Number(list.IndividualId)
                  );
                const resume =
                  allResumes &&
                  allResumes.find(
                    (user) => user.IndividualId === Number(list.IndividualId)
                  );
                const backgroundColor =
                  list && list.concurrence === ""
                    ? "#F5F2EB"
                    : list && list.concurrence === ""
                    ? "#CABD99"
                    : list && list.concurrence === ""
                    ? "#C8C8C8"
                    : "initial";
                if (list.showOn !== "off") {
                  return (
                    <div
                      className={
                        index % 2 === 0
                          ? "content_row row"
                          : "content_row row even"
                      }
                      key={index}
                    >
                      <p style={{ backgroundColor }}>{list.id}</p>
                      <p style={{ backgroundColor }}>{user.user_member_name}</p>
                      <p style={{ backgroundColor }}>
                        {user.user_member_jibunAddress}
                      </p>
                      <p style={{ backgroundColor }}>
                        {user.user_member_workType}
                      </p>
                      <p style={{ backgroundColor }}>{`${
                        resume && resume.user_resume_hopeStartYear
                      } ${resume && resume.user_resume_hopeStartMonth} ${
                        resume && resume.user_resume_hopeStartDay
                      }`}</p>
                      <p style={{ backgroundColor }}>
                        {user.user_member_career}
                      </p>
                      <p style={{ backgroundColor }}>{list.point}</p>
                      <p
                        className="btn"
                        onClick={() => {
                          handleModal(user);
                        }}
                      >
                        
                      </p>
                      {userCnt >= index + 1 && list.showOn === "on" ? (
                        <p
                          className="btn"
                          onClick={(e) => {
                            deleteList(e, list.id, userCnt);
                          }}
                        >
                          
                        </p>
                      ) : list.showOn === "on" ? (
                        <p
                          className="btn"
                          onClick={(e) => {
                            deleteList(e, list.id, userCnt);
                          }}
                        >
                          
                        </p>
                      ) : (
                        <p
                          className="btn"
                          onClick={(e) => {
                            addList(e, list.id, userCnt);
                          }}
                        >
                          
                        </p>
                      )}
                    </div>
                  );
                }
              })}
        </div>
      </div>
      {modalOpen && modalData && (
        <div className="modal">
          <p> : {modalData.user_member_name}</p>
          <p> : {modalData.user_member_tel}</p>
          <p> : {modalData.user_member_email}</p>
          <button
            onClick={() => {
              setModalOpen(false);
            }}
          >
            
          </button>
        </div>
      )}
    </div>
  );
};

export default ProcessDetail;
